@import 'src/_variables.scss';

.admin-homepage {
  text-align: center;
  min-width: 80vw;
  margin: 0 auto;

  @media (max-width: 900px) {
    margin-top: 3rem;
  }
}

.construction > img {
  animation: rotation 10s infinite linear;

  @keyframes rotation {
    0% {
      transform: rotate(0deg);
    }
    100% {
      transform: rotate(360deg);
    }
  }
}
